<template>
  <div class="suit-content" v-if="suitList.length">
    <div class="bg-tag"/>
    <div class="title">
      <span class="text">热销成套方案</span>
      <a href="javascript:;" v-if="suitList.length > 2" @click="goMore()">更多 <i></i></a>
    </div>
    <div class="suit-item" v-for="(item,index) in suitList.slice(0,2)" :key="item.id + index" @click="goDetail(item, index)">
      <div class="img-row">
        <img
          v-for="(pic,index) in item.detailList"
          :key="pic.id + index"
          class="img-item"
          :src="pic.picUrl"/>
      </div>
      <span class="suit-name">
        {{item.packageName}}
      </span>
      <div class="price-row">
        <div class="price-box">
          <div class="price">
            <span class="symbol">￥</span>
            <span class="price-int">{{item.totalPackagePrice | formatPrice | formatPriceInt}}</span>
            <span class="price-float">{{item.totalPackagePrice | formatPrice | formatPriceFloat}}</span>
          </div>
          <img class="price-img" src="//cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_pice_tag.png">
        </div>
        <div class="origin-price">
          <span class="tag">原价总计:</span>
          <span class="symbol">￥</span>
          <span class="price-int">{{item.totalStorePrice | formatPrice | formatPriceInt}}</span>
          <span class="price-float">{{item.totalStorePrice | formatPrice | formatPriceFloat}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import config from '@/config/index';
import { getSuitList } from '@/http/service/shop';
import { mapGetters } from 'vuex';
/** 预售专区 */
export default {
  props: {
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: '',
    },
    storeSign: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      suitList: [],
    }
  },
  watch: {
    storeId: {
      handler(newVal) {
        this.getSuitList();
      },
      immediate: true,
    },
    mId() {
      this.getSuitList();
    }
  },
  computed: {
    ...mapGetters(['mId']),
  },
  methods: {
    getSuitList() {
      const param = {
        keyword: '',
        pageNo: 1,
        pageSize: 5,
        storeId: this.storeId
      };
      getSuitList(param).then(res => {
        if (res.success) {
          this.suitList = res.data || [];
        }
      });
    },
    goMore() {
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '套餐购买',
        modelCode: 'prod_package',
        site_number: 'null',
        itemCode: '更多',
      })
      this.$router.push(`/shopSuitList?storeId=${this.storeId}`);
    },
    goDetail(item, index) {
      // 25093埋点
      this.$util.gioTrackWithVariable('mall_store_click_model', {
        modelName: '套餐购买',
        modelCode: 'prod_package',
        site_number: index + 1 + '',
        itemCode: item.id + '',
      })
      if (this.$util.isSgRn()) {
        window.postMessage(JSON.stringify({
          type: 'goNewWebview',
          data: {
            newUrl: `${config.HOST}/sgmobile/shopSuitDetail?storeId=${this.storeId}&packageId=${item.id}`,
          },
        }));
      } else {
        this.$router.push(`/shopSuitDetail?storeId=${this.storeId}&packageId=${item.id}`);
      }
    }
  },
}
</script>
